import {
  Component,
  Event,
  EventEmitter,
  getAssetPath,
  h,
  Host,
  Prop,
  Watch,
} from '@stencil/core';
import { createColorClasses } from '../../../utils/functions/color.function';
import { TpColor } from '../../../utils/types/color.type';
import { TpPlusMinusStatus } from './utils/plusminus.enum';

@Component({
  tag: 'tp-plusminus',
  styleUrl: 'plusminus.scss',
  assetsDirs: ['assets'],
  scoped: true,
})
export class TpPlusMinus {
  private iconMinus = getAssetPath('./assets/tp-minus-circle.svg');
  private iconPlus = getAssetPath('./assets/tp-plus-circle.svg');

  /**
   * todo
   */
  @Prop({ reflect: true }) color?: TpColor;

  /**
   * todo
   */
  @Prop({ reflect: true }) size?: 'xl';

  /**
   * todo
   */
  @Event() tpChange!: EventEmitter<TpPlusMinusStatus>;

  /**
   * todo
   */
  @Event() tpChangeAlt!: EventEmitter<number>;

  /**
   * todo
   */
  @Prop({ reflect: true }) disabled?: 'minus' | 'plus' | 'both';

  /**
   * true se deve desabilitar os controles automaticamente
   */
  @Prop({ reflect: true }) automaticDisabled = false;

  /**
   * Deverá ser true se o valor atual vai ser passado por slot, false se vai ser passado por prop
   */
  @Prop({ reflect: true }) useSlot = true;

  /**
   * O valor atual (apenas se useSlot é false)
   */
  @Prop({ reflect: true }) value?: number;

  /**
   * O valor mínimo
   */
  @Prop({ reflect: true }) min?: number;

  /**
   * O valor máximo
   */
  @Prop({ reflect: true }) max?: number;

  @Watch('value')
  disabledHandler(newValue: number, _: number) {
    if (
      this.min === undefined ||
      this.max === undefined ||
      this.value === undefined ||
      !this.automaticDisabled
    )
      return;

    if (newValue === this.min && newValue === this.max) {
      return (this.disabled = 'both');
    }

    if (newValue === this.min) {
      return (this.disabled = 'minus');
    }

    if (newValue === this.max) {
      return (this.disabled = 'plus');
    }

    this.disabled = undefined;
  }

  private onClick = (status: TpPlusMinusStatus) => {
    if (
      this.min === undefined ||
      this.max === undefined ||
      this.value === undefined ||
      !this.automaticDisabled
    ) {
      return this.tpChange.emit(status);
    }

    if (status === TpPlusMinusStatus.MINUS && this.value === this.min) return;
    if (status === TpPlusMinusStatus.PLUS && this.value === this.max) return;

    const increment = status === TpPlusMinusStatus.MINUS ? -1 : 1;
    this.tpChangeAlt.emit(this.value + increment);
  };

  componentDidLoad() {
    this.disabledHandler(this.value ?? 0, 0);
  }

  render() {
    const { iconMinus, iconPlus, size, color, disabled, useSlot, value } = this;

    return (
      <Host
        from-stencil
        class={createColorClasses(color, {
          'tp-plusminus': true,
          [`tp-plusminus--disabled-${disabled}`]: disabled !== undefined,
          [`tp-plusminus--${size}`]: size !== undefined,
        })}
      >
        <ion-icon
          class="tp-plusminus__icon tp-plusminus__icon--minus"
          src={iconMinus}
          onClick={() => this.onClick(TpPlusMinusStatus.MINUS)}
        ></ion-icon>
        {useSlot ? <slot></slot> : value ?? 0}
        <ion-icon
          class="tp-plusminus__icon tp-plusminus__icon--plus"
          src={iconPlus}
          onClick={() => this.onClick(TpPlusMinusStatus.PLUS)}
        ></ion-icon>
      </Host>
    );
  }
}
